export const getCardBackgroundImage = (type) => {
    switch (type) {
        case 'grass':
            return grass;
        case 'fire':
            return fire;
        case 'water':
            return water;
        case 'lightning':
            return lightning;
        case 'psychic':
            return psychic;
        case 'fightning':
            return fighting;
        case 'darkness':
            return darkness;
        case 'metal':
            return metal;
        case 'fairy':
            return fairy;
        default:
            return normal;
    }
}


export const getPokemonBackgroundImage = (type) => {
    switch (type) {
        case 'grass':
            return grassPokemonBackground;
        case 'fire':
            return firePokemonBackground;
        case 'water':
            return waterPokemonBackground;
        case 'lightning':
            return lightningPokemonBackground;
        case 'psychic':
            return psychicPokemonBackground;
        case 'fightning':
            return fightingPokemonBackground;
        case 'darkness':
            return darknessPokemonBackground;
        case 'metal':
            return metalPokemonBackground;
        case 'fairy':
            return fairyPokemonBackground;
        default:
            return normalPokemonBackground;
    }
}